@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&family=Squada+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/******* Font Imports *******/

/****************************/
/********** html ************/
/****************************/
html {
	scroll-behavior: smooth;
}

/****************************/
/*********** Home ***********/
/****************************/
#home {
	background-image: url(/static/media/codesitbg.df4cccc5.jpg);
	background-size: cover;
	position: relative;
}

/****************************/
/****** Section Header ******/
/****************************/
.section-header {
	margin-left: 40px;
	margin-top: 30px;
}

.header-title, .header-description {
	color: #fff;
	font-family: 'Noto Serif', serif !important;
}

.header-title {
	margin-bottom: 5px !important;
	font-size: 1.7em !important;
}

.header-description {
	text-align: right;
}

.header-line {
	margin-bottom: 2px !important;
	background-color: #fff !important;
}

.header-line:first-of-type {
	margin-right: 72px;
	margin-left: 20px;
}

.header-line:last-of-type {
	margin-left: 72px;
	margin-right: 30px;
	margin-top: 6px ;
}

.opacity-dimmer {
	position: absolute;
	width: 100vw;
	height: 100vh;
	background-color: #efefef;
	opacity: 0.2;
}

/****************************/
/******** Menu Icon *********/
/****************************/
.menu-icon {
	text-align: right;
	padding: 20px 20px 0 0;
}

/****************************/
/****** Resume Button *******/
/****************************/
#resume-btn {
	border: 1px solid #fff;
	color: #fff;
	z-index: 1;
	text-transform: none;
	font-family: 'Roboto', sans-serif;
	margin: 0 auto;
	font-weight: 400;
	margin-top: 60px;
}

#btn-container {
	text-align: center;
}

/****************************/
/******* Scroll Text ********/
/****************************/
#scroll-text {
	color: #fff;
	position: absolute;
	bottom: 7em;
	text-align: center;
	width: 100%;
	font-size: 1.5em;
	font-family: 'Squada One', cursive;
	line-height: 1.2;
}

/****************************/
/******* Scroll Icon ********/
/****************************/
.scroll-icon {
	text-align: center;
	position: absolute;
	width: 100%;
	bottom: 100px;
}

.scroll-icon svg {
	font-size: 50px;
}

#home {
	height: 100vh;
}

#skills {
	min-height: 100vh;
	width: 100vw;
}

#about {
	background-image: url(/static/media/coffeebg.89d99dc9.jpg);
	background-size: cover;
	position: relative;
}

#about-content {
	padding: 2em;
	color: #fff;
	text-align: justify;
}

#about img {
	max-width: 80%;
}

#connect-content {
	text-align: center;
	color: #fff;
	margin: 200px 0 80px 0;
}

#scroll-icon:hover, #about img:hover {
	cursor: pointer;
}

#skills-header {
	background-color: #f25555;
	padding-bottom: 20px;
}

#skills img {
	max-width: 80%;
}

.skills-inner {
	margin-bottom: 10px !important;
	align-items: center;
}

#skills-icons {
	text-align: center;
	padding: 10px 10px 0 10px;
}

.long {
	max-width: 100% !important;
}

#skills {
	background-color: #efefef;
}

#experience {
	background-color: #4a5288;
}

/* * {
	background: #000 !important;
	color: #0f0 !important;
	outline: solid #f00 1px !important;
} */

#social-icons {
	justify-content: center;
	margin-top: 20px;
}

#exp-cards {
	margin-top: 30px;
	padding: 40px;
}

/****************************/
/********* Projects *********/
/****************************/
#projects {
	background: #FF512F;
	background: linear-gradient(to right, #DD2476, #FF512F);
}

